* {
  margin: 5px 0 0 5px;
  padding: 0;
  font-family: Courgette, cursive;
}

.breeds-img {
  max-width: 500px;
  outline: 2px solid #000;
  margin-top: 20px;
  margin-left: 15px;
}

.breeds-caption {
  border-bottom: 2px solid #000;
  margin-top: 20px;
  margin-left: 15px;
}

.breeds-container {
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
  margin-top: 20px;
  margin-left: 15px;
  display: flex;
}

.breeds-title {
  color: #deb887;
}

.breeds-text {
  color: peru;
}

.breeds-link {
  color: peru;
  text-decoration: none;
}

.breeds-link:hover, .breeds-link:focus {
  color: sienna;
  border-bottom: 3px solid sienna;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #ff3d00;
  border-color: #ff3d00 rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

